import React from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';
import { editCSS, selectThemeBackgroundColor, selectThemeFontColor, selectThemeMainAccentColor, setUserTheme } from '../../store/themeSlice'; 
import { Header, ADLabel } from '../PrimaryStyles.js';

export const StyledInput = styled.input`
background-color: ${(props) => props.theme.backgroundColor};
color: ${(props) => props.theme.fontColor};

justify-self: end;
margin-right: 10%;

outline: none;
border: none;
border-bottom: 1px solid ${props => props.theme.fontColor};

&[type='color']{
    border-bottom: none;
    padding: 0;
}
`;

const validNames = ["backgroundColor", "fontColor", "mainAccentColor"]
const isValid = (name, value) => {
    return validNames.includes(name);
} 

const ThemeChanger = ({onEditCSS, onSetUserTheme, themeBackGroundColor, themeMainAccentColor, themeFontColor}) => {

    const HandleInputChange = e => {
        const {name, value} = e.target;
        if (!isValid(name, value)) return;

        onEditCSS({
            name,
            value: value
        });
        onSetUserTheme();
    }

    return (
        <>
            <Header>Change Site Theme</Header>
            <ADLabel>Background Color:</ADLabel>
            <StyledInput 
                    name='backgroundColor'
                    type='color'
                    value={themeBackGroundColor}
                    onChange={HandleInputChange}>
            </StyledInput>
            <ADLabel>Accent Color:</ADLabel>
            <StyledInput 
                    name='mainAccentColor'
                    type='color'
                    value={themeMainAccentColor}
                    onChange={HandleInputChange}>
            </StyledInput>
            <ADLabel>Font Color:</ADLabel>
            <StyledInput 
                    name='fontColor'
                    type='color'
                    value={themeFontColor}
                    onChange={HandleInputChange}>
            </StyledInput>
        </>
    );
}

const mapStateToProps = state => ({
    themeBackGroundColor: selectThemeBackgroundColor(state),
    themeMainAccentColor: selectThemeMainAccentColor(state),
    themeFontColor: selectThemeFontColor(state)
});
const mapDispatchToProps = dispatch => ({
    onEditCSS: (obj) => dispatch(editCSS(obj)),
    onSetUserTheme: () => dispatch(setUserTheme())
});

export default connect(mapStateToProps, mapDispatchToProps)(ThemeChanger);