import React, {useState} from 'react';
import styled, {keyframes} from 'styled-components';
import { connect } from 'react-redux';
import { Button } from '../PrimaryStyles.js';
import { setUserTheme, setDarkModeTheme, setLightModeTheme, selectThemeName, darkModeTheme, lightModeTheme, userTheme } from '../../store/themeSlice.js';

const StyledButton = styled(Button)`
    height: 60%;
    @media(max-width: 1000px) {
        display: none;
    }
`

const SVG = styled.svg`
    height: 60%;
    cursor: pointer;
    transition: transform 0.2s ease;
    > :first-child {
        fill: ${props => props.theme.mainAccentColor};
    }
    > :last-child {
        fill: ${props => props.theme.fontColor};
    }
    animation-name: ${(props) => props.spin? SpinAnim(): "none"};
    animation-duration: 0.2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
`

const SpinAnim = () => keyframes`
    0% { transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
`

const Container = styled.div`
    padding: 0 1rem;    
    height: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;    
`

const ThemeButton = ( {themeName, onSetUserTheme, onSetDarkTheme, onSetLightTheme, children} ) => {

    const [spin, setSpin] = useState(false);

    const changeTheme = () => {
        setSpin(true);
        if (themeName === darkModeTheme.name) {
            onSetLightTheme();
        } else if (themeName === lightModeTheme.name) {
            onSetUserTheme();
        } else if (themeName === userTheme.name) {
            onSetDarkTheme();
        }
    }

    return (
        <Container>
            <StyledButton onClick = {changeTheme} >Change Theme</StyledButton>
            <SVG onAnimationEnd={()=>setSpin(false)} spin={spin} onClick = {changeTheme} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 567.93 567.93">
                <path d="M283.46,283.46V566.93C128,566.93,0,439,0,283.46S128,0,283.46,0Z" transform="translate(0.5 0.5)"/>
                <path d="M283.46,283.46V0C439,0,566.93,128,566.93,283.46S439,566.93,283.46,566.93Z" transform="translate(0.5 0.5)"/>
            </SVG>

        </Container>
    );
}

{/* <StyledButton onClick = {changeTheme}>
{children}
</StyledButton> */}

const mapStateToProps = state => ({
    themeName: selectThemeName(state)
});
const mapDispatchToProps = dispatch => ({
    onSetUserTheme: () => dispatch(setUserTheme()),
    onSetDarkTheme: () => dispatch(setDarkModeTheme()),
    onSetLightTheme: () => dispatch(setLightModeTheme())
});

export default connect(mapStateToProps, mapDispatchToProps)(ThemeButton);