import React from 'react';
import styled from 'styled-components';
import { MainContentWrapper as MCW, GridHeader as H, GridLabel as L } from '../features/PrimaryStyles.js';
import ThemeChanger from '../features/components/ThemeChanger.js';

import { ABWrapper } from '../features/PrimaryStyles.js';
import { Header, MidHeader, Label, BlankLabel} from '../features/PrimaryStyles.js';
import { AELabel, BELabel, ACLabel, CELabel, ABLabel } from '../features/PrimaryStyles.js';
import ProjectContainer from '../features/components/Project/ProjectContainer.js';

const MainContentWrapper = styled(MCW)`
    padding: 2vh 0;
    box-sizing: border-box;
    display: grid;
    align-items: flex-start;
    justify-content: space-evenly;

    grid-template-columns: [a] 25% [b] 70% [e];
    grid-template-rows: [a] auto [b] auto [c] auto;

    row-gap: 2vh;
`

const A = styled.a`
    color: ${(props) => props.theme.mainAccentColor};
`


const Homepage = () => (
    <MainContentWrapper>
        <ABWrapper>
            <Header>Mohamad Alaili</Header>
            <AELabel>3rd Year CS student at YorkU</AELabel>
            <AELabel>(8.5/9 GPA)</AELabel>

            <BlankLabel></BlankLabel>

            <ABLabel>At:</ABLabel>
            <BELabel>North York, Ontario (CA)</BELabel>
            
            <ABLabel>Site:</ABLabel>
            <BELabel><A href = "https://alaili02.github.io/">https://alaili02.github.io/</A></BELabel>
            
            <ABLabel>Email:</ABLabel>
            <BELabel><A href = "mailto: m.alaili.1001@gmail.com">m.alaili.1001@gmail.com</A></BELabel>

            <MidHeader>Proficient In:</MidHeader>
            <Label>Java</Label>
            <Label>Python</Label>
            <Label>Bash</Label>
            <Label>C</Label>
            <Label>Javascript</Label>
            <Label>CSS</Label>
            <Label>HTML</Label>
            <Label>PHP</Label>

            <MidHeader>This site was built with:</MidHeader>
            <ACLabel>React</ACLabel>
            <CELabel>Github Pages</CELabel>
            <ACLabel>Redux</ACLabel>
            <CELabel>Styled-Components</CELabel>
        </ABWrapper>




        <ProjectContainer/>

        <ABWrapper>
            <ThemeChanger />
        </ABWrapper>
    </MainContentWrapper>
);

export default Homepage;