import { createSlice } from '@reduxjs/toolkit'

const permanentTheme = {
    navHeight: '6vh'
}
export const darkModeTheme = {
    name: 'DARK_MODE',
    fontColor: "#DDDDDD",
    textShadowColor: "#DDDDDD",
    mainAccentColor: "#32AF8C",
    backgroundColor: "#222222",
    scheduleBackgroundColor: '#222222',
}
// red on bluish gray

export const lightModeTheme = {
    name: 'LIGHT_MODE',
    fontColor: "#222222",
    textShadowColor: "#EEEEEE",
    mainAccentColor: "#FF5555",
    backgroundColor: "#DDDDDD",
    scheduleBackgroundColor: '#DDDDDD',
}

export const userTheme = {
    name: 'USER',
    fontColor: "#101010",
    textShadowColor: "#101010",
    mainAccentColor: "#7E75FF",
    backgroundColor: "#E6EDF4",
    scheduleBackgroundColor: '#E6EDF4',
}

export const themeSlice = createSlice({
    name: "theme",
    initialState: {
        ...permanentTheme,
        ...darkModeTheme,
        user: userTheme
    },
    reducers: {
        toggleTheme: (state) => {
            return (state.name === 'DARK_MODE')? 
            { ...permanentTheme, ...lightModeTheme }:
            { ...permanentTheme, ...darkModeTheme };
        },
        setDarkModeTheme: (state) => {
            return {...permanentTheme, ...darkModeTheme, user: {...userTheme, ...state.user}}
        },
        setLightModeTheme: (state) => {
            return {...permanentTheme, ...lightModeTheme, user: {...userTheme, ...state.user}}
        },
        setUserTheme: (state) => {
            return {...permanentTheme, ...state.user, user: {...userTheme, ...state.user}}
        },
        editCSS: (state, action) => {
            const { name, value } = action.payload;
            return {
                ...state,
                user: {
                    ...state.user,
                    [name]: value
                }
            }
        }
    }
})
export const selectThemeName = (state) => state.theme.name;
export const selectThemeFontColor = (state) => state.theme.fontColor;
export const selectThemeTextShadowColor = (state) => state.theme.textShadowColor;
export const selectThemeMainAccentColor = (state) => state.theme.mainAccentColor;
export const selectThemeBackgroundColor = (state) => state.theme.backgroundColor;
export const selectUserTheme = (state) => state.user;
export const { toggleTheme, editCSS, setUserTheme, setDarkModeTheme, setLightModeTheme } = themeSlice.actions;
export default themeSlice.reducer;