import styled, { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
    body {
        margin: 0;
        padding: 0;
        background-color: ${props => props.theme.backgroundColor};
        color: ${props => props.theme.fontColor};
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        transition: all 0.2s ease;
    }
`;

export const MainContentWrapper = styled.div`
    margin-top: max(${props => props.theme.navHeight}, 60px);
`;

export const GridWrapper = styled.div`
    margin: 10px auto;
    padding: 10px;

    width: fit-content;

    -webkit-box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);
    -moz-box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);
    box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);

    display: grid;
    grid-template-columns: [label-start] 20ch [label-end input-start] 12ch [input-end];
`

export const Button = styled.button`
    font-size: 1em;
    margin: 0 1rem;
    padding: 0 1em;
    border-radius: 3px;
    outline: none;
    cursor: pointer;

    /* Color the border and text with theme.main */
    background-color: ${props => props.theme.backgroundColor};
    color: ${props => props.theme.mainAccentColor};
    border: 2px solid ${props => props.theme.mainAccentColor};

    transition: border-color 0.2s ease;
    transition: background-color 0.2s ease;

    @media(hover: hover) {
        :hover {
            color: ${props => props.theme.fontColor};
            border: 2px solid ${props => props.theme.fontColor};
        }
    }
`;

export const H3 = styled.h3`

`

export const GridHeader = styled.h3`
    grid-column: label-start / input-end;
    text-align: center;
    margin: 0;
    margin-bottom: 0.5rem;
    padding: 0.25rem 0;
    border-bottom: solid 2px ${props => props.theme.mainAccentColor};
`;

export const GridLabel = styled.label`
    font-weight: bolder;
    grid-column: label-start / label-end;
    grid-row: span 1;
`;

const Wrapper = styled.div`
    position: relative;
    padding: 10px;

    -webkit-box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);
    -moz-box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);
    box-shadow: 0 0.005em 0.5em rgba(0,0,0,0.75);

    grid-column: c / e;

    display: grid;
    grid-template-columns: [a] 25% [b] 25% [c] 25% [d] 25% [e];
`

export const ABWrapper = styled(Wrapper)`
    grid-column: a / b;
    @media (max-width: 1000px) {
        grid-column: a / e;
    }
`

export const BCWrapper = styled(Wrapper)`
    grid-column: b / c;
`

export const BEWrapper = styled(Wrapper)`
    grid-column: b / E;
    @media (max-width: 1000px) {
        grid-column: a / e;
    }
`


export const Header = styled(GridHeader)`
    grid-column: a / e;
    font-size: 1.5rem;
`

export const MidHeader = styled(Header)`
    margin-top: 4vh;
    border-top: solid ${(props) => props.theme.mainAccentColor} 2px;
`


export const Label = styled.label`
    font-weight: bolder;
    grid-column: auto;
    grid-row: auto;
    font-size: larger;
    justify-content: center;
    align-items: center;
    text-align: center;
`

export const AELabel = styled(Label)`
    grid-column: a / e;
`

export const ADLabel = styled(Label)`
    grid-column: a / d;
`

export const BlankLabel = styled(AELabel)`
    height: 2vh;
`

export const ABLabel = styled(Label)`
    grid-column: a / b;
    justify-self: flex-start;'
`


export const BELabel = styled(Label)`
    grid-column: b / e;
    justify-self: flex-start;
`

export const ACLabel = styled(Label)`
    grid-column: a / c;
`

export const CELabel = styled(Label)`
    grid-column: c / e;
`