import React, { useState } from 'react';
import styled from 'styled-components';

import Project from './Project.js';

import { Header , BEWrapper } from '../../PrimaryStyles';

import { connect } from 'react-redux';

import { selectThemeBackgroundColor, selectThemeMainAccentColor, selectThemeFontColor } from '../../../store/themeSlice';
import CurrentPage from './CurrentPage.js';

const Triangle = ({ w = '30', h = '30', direction = 'right'}) => {
    const points = {
      top: [`${w / 2},0`, `0,${h}`, `${w},${h}`],
      right: [`0,0`, `0,${h}`, `${w},${h / 2}`],
      bottom: [`0,0`, `${w},0`, `${w / 2},${h}`],
      left: [`${w},0`, `${w},${h}`, `0,${h / 2}`],
    }
  
    return (
      <svg width={w} height={h}>
        <polygon points={points[direction].join(' ')} />
      </svg>
    )
}

const LeftArrow = styled.div`
    grid-column: a / b;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    width: 100%;

    cursor: pointer;
    transition: fill ease 0.2s;
    fill: ${(props) => props.theme.mainAccentColor};
    @media (hover: hover) {
        :hover {
            fill: ${(props) => props.theme.fontColor};
        }
    }
    @media (max-width: 1000px) {
        grid-row: 3 / 4;
    }   
`

const RightArrow = styled.div`
    grid-column: f / g;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    cursor: pointer;
    transition: fill ease 0.2s;
    fill: ${(props) => props.theme.mainAccentColor};
    @media (hover: hover) {
        :hover {
            fill: ${(props) => props.theme.fontColor};
        }
    }
    @media (max-width: 1000px) {
        grid-row: 3 / 4;
    }
`


const ProjectWrapper = styled(BEWrapper)`
    grid-template-columns: [a] 6% [b] 88% [f] 6% [g];
    grid-template-rows: auto 1fr;
    grid-column: b / e;
    grid-row: 1 / -1;
    @media (max-width: 1000px) {
        grid-column: a /e;
        grid-row: b / c;
        grid-template-rows: auto 1fr 5%;
    }
    transform: translateZ(0);
    position: relative;
    overflow: hidden;
`

const ModHeader = styled(Header)`
    grid-column: a / g;
`

const ProjectContainer = ({themeBackGroundColor, themeMainAccentColor}) => {

    const [page, setPage] = useState(0);

    const ChangeProject = (event, dir) => {
        if (dir == "right" && page > 0) setPage(page - 1);
        if (dir == "left" && page < 3) setPage(page + 1);
    }

    return (
        <ProjectWrapper>
            <LeftArrow onClick={event => ChangeProject(event, "right")}>
                <Triangle direction='left' color={themeMainAccentColor}/>
            </LeftArrow> 
            <ModHeader>Projects:</ModHeader>
            <RightArrow onClick={event => ChangeProject(event, "left")}>
                <Triangle direction='right' color={themeMainAccentColor}/>
            </RightArrow>

            <Project page={page} ChangeProject={ChangeProject}/>
            <CurrentPage page={page}/>
        </ProjectWrapper>
    );
}


const mapStateToProps = state => ({
    themeBackGroundColor: selectThemeBackgroundColor(state),
    themeMainAccentColor: selectThemeMainAccentColor(state),
    themeFontColor: selectThemeFontColor(state)
});
const mapDispatchToProps = dispatch => ({});


export default connect(mapStateToProps, mapDispatchToProps)(ProjectContainer);