import React, { useState } from "react";
import styled, {keyframes} from "styled-components";

const CurrentPageDiv = styled.div`
    grid-column: b / f;
    align-self: center;
    justify-self: center;
    height: 100%;
    grid-template-columns: repeat(4, 10px);
    grid-column-gap: 8px;
    grid-template-rows: 1fr;
    
    align-items: center;
    fill: ${props => props.theme.fontColor};

    position: relative;

    display: none;
    @media(max-width: 1000px) {
        display: grid;  
    }
`


const CurrentSVG = styled.svg`
    position: absolute;
    height: 10px;
    fill: ${props => props.theme.mainAccentColor};
    transform: translate(${(props) => props.page*18}px);
    transition: transform 0.1s ease;
`



const CurrentPage = ({page}) => {

    return (
        <CurrentPageDiv>
                <CurrentSVG page={page} viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="50" cy="50" r="50"/>
                </CurrentSVG>
                <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="50" cy="50" r="50"/>
                </svg>
                <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="50" cy="50" r="50"/>
                </svg>
                <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="50" cy="50" r="50"/>
                </svg>
                <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="50" cy="50" r="50"/>
                </svg>
        </CurrentPageDiv>
    );
}

export default CurrentPage;